.app__testimonial {
  flex: 1;
  width: 100%;
  flex-direction: column;
}

.app__testimonial-item {
  width: 80%;
  min-height: 320px;
  background-color: var(--white-color);
  display: flex;
  padding: 3rem;
  border-radius: 15px;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;

  img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
  }
  @media screen and (min-width: 2000px) {
    width: 60%;

    img {
      width: 150px;
      height: 150px;
    }
  }
  @media screen and (max-width: 900px) {
    width: 100%;
  }
  @media screen and (max-width: 750px) {
    flex-direction: column;
    padding: 0.625rem;
  }
}

.app__testimonial-content {
  flex: 1;
  height: 100%;

  padding: 0 2rem;
  text-align: left;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;

  P {
    font-size: 1.25rem;
    line-height: 2rem;

    color: var(--black-color);
    font-family: var(--font-base);
  }

  h4 {
    font-weight: 600;
    color: var(--secondary-color);
    margin-top: 2rem;
  }

  h5 {
    font-weight: 400;
    color: var(--gray-color);
    margin-top: 5px;
  }
}

.app__testimonial-btns {
  flex-direction: row;
  margin-top: 1rem;

  div {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: var(--white-color);

    margin: 1rem;

    transition: all 0.3s ease-in-out;

    svg {
      width: 20px;
      height: 20px;
      color: var(--secondary-color);
    }

    &:hover {
      background-color: var(--secondary-color);

      svg {
        color: var(--white-color);
      }
    }
  }
}

.app__testimonial-brands {
  width: 80%;
  flex-wrap: wrap;
  margin-top: 2rem;

  div {
    width: 150px;
    margin: 1.5rem;

    img {
      width: 80%;
      height: auto;
      object-fit: cover;
      filter: grayscale(1);
    }
    @media screen and (max-width: 750px) {
      width: 100px;
      margin: 0.625rem;

      img {
        width: 70%;
      }
    }

    &:hover {
      img {
        filter: grayscale(0);
      }
    }

    @media screen and(max-width:450px) {
      width: 120px;
      margin: 1rem;
    }
  }
  // @media screen and(max-width:1200px) {
  //   width: 150px;
  // }
}
