.app__about {
  flex: 1;
  width: 100%;
  flex-direction: column;
}
.app__profiles {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 2rem;
  // padding: 1rem;
  @media screen and (max-width: 1000px) {
    flex-direction: row;
    // flex-wrap: nowrap;
    flex-grow: 1;
    justify-content: flex-start;
  }
}

.app__profile-item {
  width: 200px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  margin: 2rem;
  @media screen and (max-width: 650px) {
    width: 100%;
  }

  @media screen and (max-width: 1000px) {
    margin: 0.625rem;
  }

  img {
    width: 100%;
    height: 170px;
    border-radius: 15px;
    object-fit: cover;
  }
  @media screen and (min-width: 2000px) {
    width: 370px;
    margin: 2rem 4rem;
    img {
      height: 240px;
    }
  }
}
